export const GridLayout = ({
  className = 'layout',
  rowHeight = 30,
  cols = 5,
  onLayoutChange = () => {},
  ...rest
}) => {
  const [columns, setColumns] = useState(cols);
  const [itemTypes, setItemTypes] = useState({});
  const [layouts, setLayouts] = useState([...generateLayout(cols)]);
  const [breakpoint, setBreakpoint] = useState('lg');
  const [compactType, setCompactType] = useState('vertical');
  const mounted = useRef(false);

  useEffect(() => {
    mounted.current = true;

    return () => {
      mounted.current = false;
    };
  }, []);

  const onCompactTypeChange = () => {
    const newCompactType =
      compactType === 'horizontal' ? 'vertical' : compactType === 'vertical' ? null : 'horizontal';
    setCompactType(newCompactType);
  };

  // const onLayoutChange = (layout, layouts) => {
  //   this.props.onLayoutChange(layout, layouts);
  // }

  const onBreakpointChange = (bp) => {
    setBreakpoint(bp);
  };

  const onNewLayout = () => {
    setLayouts(generateLayout(cols));
  };

  const onGridClear = () => {
    setLayouts([]);
  };

  const onColumnReset = () => {
    setColumns(cols);
  };

  const addColumn = () => {
    setColumns(columns + 1);
  };

  const onDrop = (layout, layoutItem, event) => {
    console.log(layoutItem, layouts.length, layout.length);
    const id = `new-${nanoid()}`;
    setItemTypes((prevState) => {
      return {
        ...prevState,
        [id]: event.dataTransfer.getData('text'),
      };
    });

    // setLayouts(layout);
    const x = layout.pop();
    setLayouts((prevState) => [
      ...layout,
      {
        ...x,
        i: id,
      },
    ]);
  };

  const onDragStart = (layout, oldItem, newItem) => {
    console.log('onDragStart', layout, oldItem, newItem);
  };

  const onDragStop = (a, b, c) => {
    console.log('onDragStop', a, b, c);
  };

  // const createElement = (el) => {
  //   const removeStyle = {
  //     position: "absolute",
  //     right: "2px",
  //     top: 0,
  //     cursor: "pointer"
  //   };
  //   const i = el.add ? "+" : el.i;
  //   return (
  //     <div key={i} data-grid={el}>
  //       {el.add ? (
  //         <span
  //           className="add text"
  //           onClick={this.onAddItem}
  //           title="You can add an item by clicking here, too."
  //         >
  //           Add +
  //         </span>
  //       ) : (
  //         <span className="text">{i}</span>
  //       )}
  //       <span
  //         className="remove"
  //         style={removeStyle}
  //         onClick={this.onRemoveItem.bind(this, i)}
  //       >
  //         x
  //       </span>
  //     </div>
  //   );
  // };

  const children = useMemo(() => {
    return _.map(layouts, (l, i) => {
      console.log(l, i, itemTypes);
      return (
        <Box display="flex" data-grid={{ ...l }} key={l.i} className={l.static ? 'static' : ''}>
          {itemTypes[l.i] === 'editor' ? (
            <Box flex={1}>
              <RichTextInput fullHeight initialValue={{ html: l.i.substring(0, 9) }} />
            </Box>
          ) : (
            <span className="text">
              {l.static ? 'STATIC -' : ''} {l.i.substring(0, 9)}
            </span>
          )}
        </Box>
      );
    });
  }, [layouts]);

  return (
    <Box sx={{ display: 'flex' }}>
      <Box component="main" sx={{ flexGrow: 1, bgcolor: 'background.default', p: 3 }}>
        <div style={{ width: `${210 * columns}px` }} className={className}>
          <Box
            sx={{
              position: 'relative',
              height: 80,
              width: '100%',
              top: 0,
              left: 0,
              p: 3,
            }}
            className="controls"
          >
            <Box className="actions">
              <Button variant="outlined" sx={{ mr: 1 }} onClick={onNewLayout}>
                Generate New Layout
              </Button>
              <Button variant="outlined" sx={{ m: 1 }} onClick={onCompactTypeChange}>
                Change Compaction Type
              </Button>
              <Button variant="outlined" sx={{ m: 1 }} onClick={addColumn}>
                Add column
              </Button>
              <Button variant="outlined" color="error" sx={{ m: 1 }} onClick={onGridClear}>
                Clear grid
              </Button>
              <Button variant="outlined" color="error" sx={{ m: 1 }} onClick={onColumnReset}>
                Reset columns
              </Button>
            </Box>
            <Box sx={{ my: 2 }} className="meta">
              <Box fontSize="14px">Columns: {columns}</Box>
              <Box fontSize="14px">
                Compaction type: {_.capitalize(compactType) || 'No Compaction'}
              </Box>
            </Box>
          </Box>
          <Box sx={{ marginTop: '40px' }} className="columns">
            {_.range(columns).map((value) => {
              return (
                <div key={nanoid()} className={`column-${value + 1}`}>
                  Col {value + 1}
                </div>
              );
            })}
          </Box>
          <ReactGridLayout
            {...rest}
            width={210 * columns}
            layout={layouts}
            margin={[10, 10]}
            rowHeight={200}
            cols={columns}
            // droppingItem={{ i: `new-${nanoid()}` }}
            onBreakpointChange={onBreakpointChange}
            onLayoutChange={onLayoutChange}
            // WidthProvider option
            measureBeforeMount={false}
            isDroppable
            onDrop={onDrop}
            onDragStart={onDragStart}
            // onDropDragover={onDropDragover}
            onDragStop={onDragStop}
            // I like to have it animate on mount. If you don't, delete `useCSSTransforms` (it's default `true`)
            // and set `measureBeforeMount={true}`.
            useCSSTransforms={mounted.current}
            compactType={compactType}
            preventCollision={!compactType}
          >
            {/* <div key="a">a</div>
        <div key="b">b</div>
        <div key="c">c</div> */}
            {children}
          </ReactGridLayout>
        </div>
      </Box>
      <Drawer
        sx={{
          width: 300,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            padding: '10px',
            width: 300,
            boxSizing: 'border-box',
          },
          '& .droppable-element': {
            width: '50px',
            height: '50px',
            backgroundColor: '#EBEBEB',
            border: '1px solid #CDCFCF',
            margin: '10px 5px',
            padding: '10px',
            display: 'inline-flex',
            justifyContent: 'center',
            alignItems: 'center',
          },
        }}
        variant="permanent"
        anchor="right"
      >
        <Box fontSize="14px" className="factory">
          <div>Drag to create elements</div>
          <div
            onDragStart={(e) => e.dataTransfer.setData('text/plain', 'editor')}
            className="droppable-element"
            draggable
            unselectable="on"
          >
            Editor
            <EditIcon />
          </div>
          <div
            onDragStart={(e) => e.dataTransfer.setData('text/plain', 'blank')}
            className="droppable-element"
            draggable
            unselectable="on"
          >
            Blank
          </div>
          <div
            onDragStart={(e) => e.dataTransfer.setData('text/plain', 'row')}
            className="droppable-element"
            draggable
            unselectable="on"
          >
            Row
          </div>
        </Box>
      </Drawer>
    </Box>
  );
};
