/* eslint-disable */
// @ts-nocheck
import React from 'react';
import { colors } from '../theme';
import Box from '@mui/material/Box';
import { IconButton, IconButtonProps, Icon } from '../Icon';
import { useMutationObserver } from './useMutationObserver';

type ToolbarButtonProps = { className?: string } & IconButtonProps;

const Bold = ({ className }: ToolbarButtonProps) => (
  <IconButton className={className}>
    <Icon fontVariant="boldXS" iconPrefix="fas" iconName="bold" />
  </IconButton>
);

const Italic = ({ className }: ToolbarButtonProps) => (
  <IconButton className={className}>
    <Icon fontVariant="boldXS" iconPrefix="fas" iconName="italic" />
  </IconButton>
);

const List = ({ className }: ToolbarButtonProps) => (
  <IconButton className={className}>
    <Icon fontVariant="boldXS" iconPrefix="fas" iconName="list" />
  </IconButton>
);

const toolbarButtons = {
  bold: ({ className }: ToolbarButtonProps) => <Bold className={className} />,
  italic: ({ className }: ToolbarButtonProps) => <Italic className={className} />,
  list: ({ className }: ToolbarButtonProps) => <List className={className} />,
};

export type ToolbarProps = {
  id: string;
  onDOMElementRendered?: (el: HTMLElement) => void;
  buttonClassNames?: {
    [key in keyof typeof toolbarButtons]: string;
  };
  buttonActiveClassName?: string;
};

export const Toolbar = ({
  buttonActiveClassName,
  buttonClassNames,
  id,
  onDOMElementRendered,
}: ToolbarProps) => {
  const sx = buttonActiveClassName && {
    [`.${buttonActiveClassName}`]: { color: colors.blue[600] },
  };
  const ref = useMutationObserver(onDOMElementRendered);
  return (
    <Box ref={ref} id={id} sx={sx}>
      {Object.keys(toolbarButtons).map((buttonName) => {
        const Button = toolbarButtons[buttonName] as React.FunctionComponent<ToolbarButtonProps>;
        return (
          <React.Fragment key={buttonName}>
            <Button
              disableRipple={false}
              className={buttonClassNames && buttonClassNames[buttonName]}
            />
          </React.Fragment>
        );
      })}
    </Box>
  );
};
