/* eslint-disable */
// @ts-nocheck
import React from 'react';
import SvgIcon, { SvgIconProps } from '@mui/material/SvgIcon';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faCoffee,
  faPlusCircle,
  faArrowAltCircleRight,
  faTrash,
  faEdit,
  faArrowRight,
  faTimes,
  faTimesCircle,
  faChevronDown,
  faChevronUp,
  faQuestionCircle,
  faUser,
  faCheck,
  faCheckDouble,
  faInfoCircle,
  faArrowCircleRight,
  faEnvelope,
  faExclamationTriangle,
  faPercentage,
  faStarOfLife,
  faBold,
  faItalic,
  faList,
  faCheckCircle,
} from '@fortawesome/free-solid-svg-icons';
import {
  IconName,
  IconPrefix,
  library,
  findIconDefinition,
  IconDefinition,
} from '@fortawesome/fontawesome-svg-core';
import {
  faSearch as falSearch,
  faTimes as falTimes,
  faCheck as falCheck,
} from '@fortawesome/pro-light-svg-icons';
import {
  faExclamationCircle,
  faQuestionCircle as fasQuestionCircle,
} from '@fortawesome/pro-solid-svg-icons';
import { fontVariants } from '../theme/foundations/typography';

export const ICONS: IconDefinition[] = [
  faArrowAltCircleRight,
  faCoffee,
  faPlusCircle,
  faTrash,
  faEdit,
  faArrowRight,
  faTimes,
  falTimes,
  falSearch,
  faTimesCircle,
  faChevronUp,
  faChevronDown,
  faQuestionCircle,
  faUser,
  faCheck,
  falCheck,
  faCheckDouble,
  faArrowCircleRight,
  faInfoCircle,
  faEnvelope,
  faExclamationTriangle,
  faPercentage,
  faStarOfLife,
  fasQuestionCircle,
  faBold,
  faItalic,
  faList,
  faExclamationCircle,
  faCheckCircle,
];

library.add(...ICONS);

function getIcon(iconName: IconName, prefix: IconPrefix = 'fas'): IconDefinition {
  return findIconDefinition({ iconName, prefix });
}

export interface IconProps extends Omit<SvgIconProps, 'color'> {
  iconName: IconName;
  iconPrefix?: IconPrefix;
  color?: string;
  fontVariant?: keyof typeof fontVariants | 'inherit';
}

export const Icon = ({
  iconName,
  iconPrefix,
  color = 'inherit',
  fontVariant = 'inherit',
  ...other
}: IconProps): JSX.Element => {
  return (
    <SvgIcon sx={{ color, ...fontVariants[fontVariant] }} {...other}>
      <FontAwesomeIcon icon={getIcon(iconName, iconPrefix)} />
    </SvgIcon>
  );
};
